import { useLocation, useSearchParams } from "react-router-dom";
import GoBack from "../../../Shared/StaffStudentProfile/GoBack";
import UserMemberMainWrapper from "../../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { Suspense, lazy } from "react";
import DepartmentSiteInfoProfile from "./DepartmentSiteInfoProfile";
import UserMemberTabsWrapper from "../../../Ui/UserMember/UserMemberTabsWrapper";
import InstituteMemberConfig from "../../../InstituteMember/Config/InstituteMemberConfig";
import { useDepartmentDashboardSite } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import NavbarConfig from "../../../InstituteMember/Config/NavbarConfig";
import DepartmentSiteListAbout from "./DepartmentSiteListAbout/DepartmentSiteListAbout";
const DepartmentSiteInfoAbout = lazy(() => import("./DepartmentSiteInfoAbout"));
const DepartmentSitePso = lazy(() =>
  import("./DepartmentSitePso/DepartmentSitePso")
);
const DepartmentSiteProfessional = lazy(() =>
  import("./DepartmentSiteProfessional/DepartmentSiteProfessional")
);
const DepartmentSiteProject = lazy(() =>
  import("./DepartmentSiteProject/DepartmentSiteProject")
);
const DepartmentSiteStudentAchievement = lazy(() =>
  import("./DepartmentSiteStudentAchievement/DepartmentSiteStudentAchievement")
);
const DepartmentSiteStudentAssociation = lazy(() =>
  import("./DepartmentSiteStudentAssociation/DepartmentSiteStudentAssociation")
);
const DepartmentSiteInnovativePractice = lazy(() =>
  import("./DepartmentSiteInnovativePractice/DepartmentSiteInnovativePractice")
);
const DepartmentSiteSyllabus = lazy(() =>
  import("./DepartmentSiteSyllabus/DepartmentSiteSyllabus")
);

const DepartmentSiteInfoConfig = ({ did, viewAs, isNavbar }) => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { departmentDashboardSite } = useDepartmentDashboardSite({
    data: {
      did: did,
    },
    skip: !did,
  });
  return (
    <>
      <NavbarConfig viewAs={viewAs} isNavbar={isNavbar}>
        <InstituteMemberConfig>
          <section
            style={{
              width: "100%",
            }}
          >
            <GoBack
              customStyle={{
                width: "100%",
                marginTop: "0",
                marginBottom: "1rem",
              }}
            />
            <UserMemberMainWrapper>
              <Suspense fallback={<QvipleLoader />}>
                {!searchParams.get("type") ? (
                  <UserMemberTabsWrapper>
                    <DepartmentSiteInfoProfile qcid={getQuery.state?.qcid} />
                  </UserMemberTabsWrapper>
                ) : searchParams.get("type") === "about" ? (
                  <DepartmentSiteInfoAbout did={did} />
                ) : searchParams.get("type") === "list-about" ? (
                  <DepartmentSiteListAbout
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "pso" ? (
                  <DepartmentSitePso
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "body" ? (
                  <DepartmentSiteProfessional
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "association" ? (
                  <DepartmentSiteStudentAssociation
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "achievement" ? (
                  <DepartmentSiteStudentAchievement
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "practice" ? (
                  <DepartmentSiteInnovativePractice
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "syllabus" ? (
                  <DepartmentSiteSyllabus
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : searchParams.get("type") === "project" ? (
                  <DepartmentSiteProject
                    dsid={departmentDashboardSite?._id}
                    did={did}
                  />
                ) : null}
              </Suspense>
            </UserMemberMainWrapper>
          </section>
        </InstituteMemberConfig>
      </NavbarConfig>
    </>
  );
};

export default DepartmentSiteInfoConfig;
