import InstituteNavbarConfig from "./InstituteNavbarConfig";
import UserNavbarConfig from "./UserNavbarConfig";
import style from "../InstituteMember.module.css";

const NavbarConfig = ({ viewAs, isNavbar, children }) => {
  return (
    <>
      {viewAs === "INSTITUTE" ? (
        <>
          {isNavbar ? (
            <InstituteNavbarConfig>{children}</InstituteNavbarConfig>
          ) : (
            <div className={style.main_container}>{children}</div>
          )}
        </>
      ) : (
        <UserNavbarConfig>{children}</UserNavbarConfig>
      )}
    </>
  );
};

export default NavbarConfig;
